.label-text {
  padding: 0 0 0 16px;

  @include font-base;
}

.label-text--phone {
  position: relative;
}

.label-text--phone::after {
  content: "";
  position: absolute;
  background-image: url("/img/icons/phone.svg");
  width: 58px;
  height: 58px;
  top: 20px;
  left: 222px;

  @include desktop {
    left: 344px;
  }
}

.label-text--email {
  position: relative;
}


.label-text--email::after {
  content: "";
  position: absolute;
  background-image: url("/img/icons/envelope.svg");
  width: 58px;
  height: 59px;
  top: 19px;
  left: 222px;

  @include desktop {
    left: 344px;
  }
}

.label-text--special {
  color: $font-color-secondary;

  @include font-base;
}
