@mixin font-base($weight: 400) {
  font-size: 14px;
  line-height: 21px;
  font-style: normal;
  font-weight: $weight;
  text-transform: uppercase;
}

@mixin font-bold {
  font-size: 18px;
  line-height: 26px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}

@mixin font-button {
  font-size: 21px;
  line-height: 26px;
  font-style: normal;
  font-weight: 700;
  text-transform: uppercase;
}

@mixin fieldset-decorate {
  border: none;
  margin: 0;
  border-bottom: 1px solid #e5e5e5;
}

@mixin tablet {
  @media screen and (min-width: $tablet-width) {
    @content;
  }
}

@mixin desktop {
  @media screen and (min-width: $desktop-width) {
    @content;
  }
}
